.header{
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: 1px solid black;
}

.nav-items > ul{
    display: flex;
    list-style-type: none;
}

.nav-items > ul > li{
    padding: 10px;
}

.logo{
    width: 100px;
}

.restaurant-list{
    width:90%;
    margin:2rem  auto;
    display:flex;
    flex-wrap:wrap;
    justify-content:center;
    gap: 1rem;
}
.card {
    padding: 1rem;
    width:200px;
    border-radius:5px;
    box-shadow:5px 5px 10px 0 gray;
    overflow-wrap: break-word;
}

.card > img {
    width:100%;
    border-radius:5px;
}